import React from 'react'
import Link from 'gatsby-link'

const IndexPage = () => (
  <div>
    <h1>This is the transcript of the great Coloring Book by Chance</h1>
    <p>Go listen.</p>
    <Link to="/songs/">Songs</Link>
  </div>
)

export default IndexPage



